const setFavourList = (state) => {
  const { favourList } = state
  const favourListString = JSON.stringify(favourList)
  localStorage.favourList = favourListString
}
export default {
  addFavour(state, fullTextResult) {
    state.favourList.push(fullTextResult)
    setFavourList(state)
  },
  deFavour(state, fullTextResult) {
    state.favourList.remove(fullTextResult)
    setFavourList(state)
  }
}



