export function addFavour({ commit, state }, fullTextResult) {
  commit('addFavour', fullTextResult)
}

export function deFavour({ commit, state }, fullTextResult) {
  commit('deFavour', fullTextResult)
}

export default {
  addFavour,
  deFavour
}



